@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Rowdies:wght@300&family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");
*{
  font-family: "spartan", sans-serif; 
}
.ProductDetails {
  background-color: rgb(255, 255, 255);
  width: 100vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
}

.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: 1px solid white;
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}

.CarouselImage {
  width: 35vmax;
}
.testing{
  position: relative;
}

.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
  font-weight: 600;
  font-size: 1.6vmax;
}

.detailsBlock-1 > p {
  color: rgba(54, 54, 54, 0.582);
  font: 200 0.6vmax cursive;
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 70%;
  padding: 1vmax 0;
}

.detailsBlock-2-span {
  font: 200 0.8vmax cursive;
  color: rgba(0, 0, 0, 0.699);
}

.detailsBlock-3 {
  width: 70%;
}

.detailsBlock-3 > h1 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax "Franklin Gothic Medium";
  margin: 1vmax 0;
}
.h2Style {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.5vmax "Franklin Gothic Medium";
  margin: 0.8vmax 0;
  display: flex;
  flex-direction: row;
}

.h2Style > p{
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.5vmax "Franklin Gothic Medium";
  text-decoration: line-through;
  margin-left: 0.3vmax;
}
.h2Style strong{
  color: rgb(255, 0, 0);
  font: 400 1.5vmax "Franklin Gothic Medium";
  margin-left: 0.3vmax;
}
.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}

.select{
  display: block;
  padding: 5px 10px;
  margin-bottom: 10px;
  color: black;
  background-color: white;
  border-radius: 4px;
  border: 1px solid black;
}
.name{
  width: 138.717px;
  display: block;
  padding: 5px 10px;
  margin-bottom: 10px;
  color: rgb(0, 0, 0);
  background-color: white;
  border-radius: 4px;
  border: 1px solid black;
  outline: none;
}
.detailsBlock-3-1-1{
  display: block;
}
.detailsBlock-3-1-1 > button {
  
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}
.detailsBlock-3-1-1 > button:hover {  
  background-color: rgba(0, 0, 0, 0.767);
}

.detailsBlock-3-1-1 > input {
  border: none;
  padding: 0.5vmax;
  width: 1vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax ;
  color: rgba(0, 0, 0, 0.74);
}
  

.detailsBlock-3-1 > button:last-child {
  border: none;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  background-color: #fa6643;
  font: 500 0.7vmax ;
  border-radius: 20px;
  padding: 0.5vmax 2vmax;
  margin: 1vmax;
  outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
  background-color: rgb(214, 84, 61);
}

.detailsBlock-3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1vmax ;
  margin: 1vmax 0;
}

.detailsBlock-4 {
  color: rgba(0, 0, 0, 0.897);
  font: 500 1.2vmax sans-serif;
}

.detailsBlock-4 > p {
  color: rgba(0, 0, 0, 0.534);
  font: 300 0.8vmax sans-serif;
}

.submitReview {
  border: none;
  background-color: #fa6643;
  font: 500 0.7vmax ;
  border-radius: 20px;
  padding: 0.6vmax 2vmax;
  margin: 1vmax 0;
  color: white;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}
.submitReview:hover {
  background-color: rgb(197, 68, 45);
  transform: scale(1.1);
}

.submitDialog {
  display: flex;
  flex-direction: column;
}
.submitDialogTextArea {
  border: 1px solid rgba(0, 0, 0, 0.082);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem ;
}

.reviewsHeading {
  color: #000000be;
  font: 500 1.4vmax ;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}
.reviews {
  display: flex;
  overflow: auto;
}

.reviewCard {
  flex: none;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.226);
  border: 1px solid rgba(56, 56, 56, 0.116);
  width: 30vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1vmax;
  padding: 3vmax;
}
.reviewCard > img {
  width: 5vmax;
}
.reviewCard > p {
  color: rgba(0, 0, 0, 0.836);
  font: 600 0.9vmax ;
}
.reviewCardComment {
  color: rgba(0, 0, 0, 0.445);
  font: 300 0.8vmax cursive;
}

.noReviews {
  font: 400 1.3vmax;
  text-align: center;
  color: rgba(0, 0, 0, 0.548);
}


@media screen and (max-width: 600px) {
  .ProductDetails {
    flex-direction: column;
    height: unset;
  }

  .ProductDetails > div:last-child {
    align-items: center;
  }
  .h2Style{
    flex-direction: column;

  }
  
  .detailsBlock-1 > h2 {
    font-size: 2.8vmax;
    text-align: center;
  }

  .detailsBlock-1 > p {
    text-align: center;
    font-size: 1vmax;
  }
  

  .detailsBlock-2 {
    justify-content: center;
  }
  .detailsBlock-2 > span {
    font-size: 1.5vmax;
  }
  
  .detailsBlock-3 > h1 {
    font: 700 3vmax;
    text-align: center;
  }
  

  .select,
  .name{
    transform: translate(14vw);
  }
  .detailsBlock-3-1 {
    flex-direction: column;
  }

  .detailsBlock-3-1-1 {
    padding: 2vmax 0;
  }
  .detailsBlock-3-1-1 > button {
    padding: 1.2vmax;
    width: 4vmax;
  }

  .detailsBlock-3-1-1 > input {
    padding: 1.5vmax;
    width: 3vmax;
    font: 400 1.8vmax ;
  }

  .detailsBlock-3-1 > button:last-child {
    font: 500 1.7vmax ;
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .detailsBlock-3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax ;
  }

  .detailsBlock-4 {
    font: 500 2.5vmax sans-serif;
  }

  .detailsBlock-4 > p {
    font: 300 1.8vmax sans-serif;
  }

  .submitReview {
    font: 500 1.7vmax ;
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .reviewCard > p {
    font: 600 3vw ;
  }
  .reviewCardComment {
    font: 300 5vw cursive;
  }
} 
@media screen and (max-width: 414px) {
  .select,
  .name{
    transform: translate(6vw);
  }
  .CartItemCardCK{
    height: 40vh;
  }
}